<template>
  <div id="timecard">
    <div id="headingdiv">
      <h1 id="heading">{{ this.card }}</h1>
    </div>

    <div id="total">
      {{ this.totalSeconds / 1000 }} seconds
    </div>

    <div id="section">
      <div id="section1">
        <countdown :cur="this.days" :until="365" :title="'days'" :mode="null"/>
        <countdown :cur="this.minutes" :until="60" :title="'minutes'" :mode="null"/>
      </div>

      <div id="section2">
        <countdown :cur="this.hours" :until="24" :title="'hours'" :mode="null"/>
        <countdown :cur="this.seconds" :until="60" :title="'seconds'" :mode="null"/>
      </div>
    </div>
  </div>

</template>

<script>

import countdown from "@/components/countdown.vue";

export default {
  props: ["card"],
  components: {countdown},
  name: "time_card",
  data() {
    return {
      totalSeconds: 0
    }
  },
  computed: {
    days() {
      return Math.floor(this.totalSeconds / 86400000);
    },
    hours() {
      return this.diff.getHours() - 1;
    },
    minutes() {
      return this.diff.getMinutes();
    },
    seconds() {
      return this.diff.getSeconds();
    },
    diff() {
      return new Date(this.totalSeconds);
    }
  },

  methods: {

  },

  watch: {

  },
  created() {
    let mainLoop = setInterval(() =>{
      let now = Date.now();
      this.totalSeconds = this.$store.state.finalDate - now;
    }, 100);
  }
}

</script>

<style scoped lang="sass">

@import "../assets/variables.sass"

#timecard
  width: max-content
  display: flex
  align-items: center
  flex-direction: column

#total
  font-size: 1.4rem

#headingdiv
  margin: 1rem 1rem 0 1rem
  font-size: 1.4rem

#section
  display: flex

#section1, #section2
  margin-bottom: 1rem

</style>