<template>

  <div id="countdown">

    <div id="percentage">
      {{ percentage }}
    </div>

    <div id="title">
      {{ this.cur }}
      {{ this.title }}
    </div>

  </div>

</template>

<script>

export default {
  props: ["cur", "until", "title", "mode"],
  name: "countdown",
  data() {
    return {

    }
  },
  computed: {
    percentage() {

      let current;
      if (this.cur === null) {
        current = 0;
      } else {
        current = this.cur;
      }

      return this.truncate(current / this.until);
    },
  },

  methods: {
    truncate(number) {
      return number.toString().match(/^-?\d+(?:\.\d{0,3})?/)[0];
    }
  },

  watch: {

  },
}

</script>

<style lang="sass" scoped>
@import "../assets/variables.sass"

#countdown
  width: 10vw
  display: flex
  flex-direction: column

  align-items: center

  border: 2px solid $base-font-color
  border-radius: 10px

  backdrop-filter: blur(1rem)

  box-shadow: $base-shadow
  margin: 1rem

  font-size: 1.4rem

#countdown:hover
  cursor: pointer

#countdown:active
  box-shadow: none

#percentage
  padding-top: 2.5rem
  padding-bottom: 1rem

#current
  padding-right: 4px

#title
  font: $base-font
  color: $base-font-color

</style>